import React from 'react';
import orderDetailsStyle from './order-details.module.css'
import orderAccepted from '../../images/order_accpeted.svg'
import { useSelector } from 'react-redux';


const OrderDetails = () => {
	const  orderNumber  = useSelector(store => store.order.number);
	return (
		<div className={`${orderDetailsStyle.container} pl-25 pr-25`}>
			<h1 className={`${orderDetailsStyle.title} text text_type_digits-large pt-15 pb-8`}>{orderNumber}</h1>
			<p className={`${orderDetailsStyle.text} text text_type_main-medium pb-15`}> </p>
			<img className={`${orderDetailsStyle.icon} pb-15`} src={orderAccepted} alt={orderAccepted} />
			<p className={`${orderDetailsStyle.text} text text_type_main-default pb-2`}>   </p>
			<p className={`${orderDetailsStyle.text} text text_type_main-default text_color_inactive pb-30`}>    </p>
		</div>
	)
}

export default OrderDetails;